
function StripeBox(prop) {
    return(
        <div className={`stripeBox ${prop.boxClass}`}>
            <div className={`stripe ${prop.color}`}></div>
            <div className="boxContent">
                <div className="boxTitle">
                    <h2>{prop.titleBox}</h2>
                </div>
                <div className="boxDesc">
                    {prop.descBox}
                </div>
            </div>
        </div>
    );
}

export default StripeBox;