import Card from "./Card";
import htmlImg from "../assets/lang/html.svg";
import cssImg from "../assets/lang/css3.svg";
import jsImg from "../assets/lang/js.png";
import phpImg from "../assets/lang/php.logo.jpg";
import sqlImg from "../assets/lang/mysql.svg";
import pythonImg from "../assets/lang/python.svg";

const htmlTitle = "HTML5";
const htmlDesc = "Good handling of HTML, use of structured and ordered code. Neatly containerizing, working with SEO, and applying most of my knowledge in web design to achieve an elegant web page.";

const cssTitle = "CSS3";
const cssDesc = "I know the fundamentals and basics of adaptive design. I worked with a complete web page with several sections in which I worked both the adaptive design and the entire layout. My next wish is to learn Bootstrap or SASS";

const jsTitle = "Javascript";
const jsDesc = "Using pure JS for edit the DOM, and some actions. Good experience on jQuery and AJAX for send data on real time. Good use of language to manipulate the DOM and schedule certain events. Using asynchronous programming to improve product quality";

const phpTitle = "PHP";
const phpDesc = "Using pure JS for edit the DOM, and some actions. Good experience on jQuery and AJAX for send data on real time. Good use of language to manipulate the DOM and schedule certain events. Using asynchronous programming to improve product quality";

const sqlTitle = "MySQL";
const sqlDesc = "I used things like queries, insertions and deletion of data in my projects (I created a PHP OOP library, which allowed me to include it in every project that I was going to work on). I preferred mySQL because I needed to save the data in a relational, orderly way and so that later it can be accessed by a backend language that I am going to use, I think it is a great tool that allows me to manipulate databases in an incredible way.";

const pythonTitle = "Python 3";
const pythonDesc = "I generally consider this language to be one of the most useful when it comes to automating. I used many modules, for example, generating strong passwords in the field of cibersecurity. To create custom GUI applications. Use of databases with the SQlite3 module. I use basic Flask for test any projects";


function Abilities() {
    return (
        <div className="abilitiesSections">
            <div className="abilitiesBox">
                <div className="orangeBox"></div>
                <div className="abilitiesTitle">
                    <h1>My Abilities: Languages</h1>
                </div>
            </div>
            <div className="abilitiesCard">
                <Card classCard="htmldiv" imgsource= {htmlImg} title={htmlTitle} description={htmlDesc} /> 
                <Card  classCard="cssdiv" imgsource= {cssImg} title={cssTitle} description={cssDesc} />
                <Card  classCard="jsdiv" imgsource= {jsImg} title={jsTitle} description={jsDesc} />
                <Card  classCard="phpdiv" imgsource= {phpImg} title={phpTitle} description={phpDesc} />
                <Card  classCard="mysqldiv" imgsource= {sqlImg} title={sqlTitle} description={sqlDesc} />
                <Card  classCard="pydiv" imgsource= {pythonImg} title={pythonTitle} description={pythonDesc} />
            </div>
        </div>
    );
}

export default Abilities;