import $ from 'jquery';

function Menu() {
    return (
        <div className="menu">
            <ul>
                <li className="element">
                    <a className="first top-line" href="https://leandroblandi.github.io/portfolio-react/#">
                        <i className="fas fa-home icon-menu" aria-hidden="true"></i>
                        Home
                    </a>
                </li>
                <li className="element">
                    <a className="sec bottom-line" href="https://leandroblandi.github.io/portfolio-react/#social-network-list">
                        <i className="fas fa-address-book icon-menu" aria-hidden="true"></i>
                        Contact
                    </a>
                </li>
            </ul>
        </div>
    );
}

function handleClickMenu(e){
    $(".menu").toggle(150, "linear");
    e.preventDefault();
}

function IconMenu(){
    return (
        <div className="menuBar">
            <i class="fas fa-bars iconMenu" onClick={handleClickMenu}></i>
        </div>  
    );
}

function Header() {
    return (
        <div className="headerSection">
            <IconMenu />
            <Menu />
        </div>
    );
}

export default Header;