import projectImg from "../assets/projects/byg-presentation.png"

const titleProject = "Web development";
const statusProject = "finished";
const descProject = "The work consists of developing a complete web page: with a gallery, a contact section with a contact form that works in a dynamic way, adaptable to cell phones, tablets, etc.";

const otherProjectsTitle = "Other projects";
const otherProjectsDesc = "In the professional field I have only one experience. But on a personal level (as a programmer) I can show you more: They can access my Github account and see my repositories, there I continually upload content.";


function EachProject(prop) {
    return (
        <div className="eachProject">
            <div className="project">
                <div className="bigTitleProject">
                    <h2>Web development</h2>
                </div>
                <ul className="projectDetails">
                    <li className="titleProject"><b>Project name:</b>&nbsp;{prop.title}</li>
                    <li className="statusProject"><b>Status:</b>&nbsp;{prop.status}</li>
                    <li className="descProject"><b>Project description:</b>&nbsp;{prop.description}</li>
                </ul>
            </div>
            <div className="imgProject">
                <img src={prop.imgsource} alt="Project" />
            </div>
        </div>
    );
}

function SimpleCard(prop){
    return(
        <div className="simpleCard">
            <div className="titleSimpleCard">
                <h3>{prop.title}</h3>
            </div>
            <div className="descSimpleCard">
                <p>{prop.description}</p>
            </div>
        </div>
    );
}

function ProjectSide() {
    return (
        <div className="projectSection">
            <div className="projectBox">
                <div className="greenBox"></div>
                <div className="projectTitle">
                    <h2>My Projects</h2>
                </div>
            </div>
            <EachProject title={titleProject} status={statusProject} description={descProject} imgsource={projectImg} />
            <SimpleCard title={otherProjectsTitle} description={otherProjectsDesc} />
        </div>
    );
}

export default ProjectSide;