import profilephoto from "../assets/profile.jpg"

const name = "Leandro Blandi"
const myDescription = "Argentinian, 18 years old, web designer and backend programmer. I love learning new technologies and backend development. Looking for job opportunities, and wanting to get new experiences. I love Raspberry's."

function PresentationCard(){
    return (
        <div className="presentationCardSection">
            <div className="titlePresentation">
                <h1>{name}</h1>
            </div>
            <div className="photoPresentation">
                <img src={profilephoto} alt="My profile" />
            </div>
            <div className="descriptionPresentation">
                <p>{myDescription}</p>
            </div>
        </div>
    );
}

export default PresentationCard;