import StripeBox from "./StripeBox"

const contactSideBoxClass = "contact-side-box"
const contactSideStripeColor = "blue"
const contactSideTitle = "Contact me";
const contactSideDesc = "Hi, if you have a question about code, send me a direct message via Instagram. Or you can also see my Github or my StackOverflow profile in the section below.";

const socialNetworkBoxClass = "social-network";
const socialNetworkStripeColor = "yellow";
const socialNetworkTitle = "Social network";
const socialNetworkDesc = (
    <ul id="social-network-list">
        <li className="element">
            <i className="fab fa-instagram" aria-hidden="true"></i>
            <a className="link" href="https://www.instagram.com/leandroblandi/">Follow me on Instagram</a>
        </li>
        <li className="element">
            <i className="fab fa-github" aria-hidden="true"></i>
            <a className="link" href="https://github.com/leandroblandi">See my Github profile</a>
        </li>
        <li className="element">
            <i className="fab fa-stack-overflow" aria-hidden="true"></i>
            <a className="link" href="https://stackoverflow.com/users/14716565/leandro-blandi">My StackOverflow profile</a>
        </li>
    </ul>
);

function ContactSide() {
    return (
        <div className="contactSection">
            <StripeBox boxClass={contactSideBoxClass} color={contactSideStripeColor} titleBox={contactSideTitle} descBox={contactSideDesc}/>
            <StripeBox boxClass={socialNetworkBoxClass} color={socialNetworkStripeColor} titleBox={socialNetworkTitle} descBox={socialNetworkDesc}  />
        </div>
    );

}

export default ContactSide;