
function Card(prop){
    return (
        <div className="card">
            <div className={`imgCard ${prop.classCard}`}>
                <img src={prop.imgsource} alt="Card"/>
            </div>
            <div className="titleCard">
                <h2>{prop.title}</h2>
            </div>
            <div className="descriptionCard">
                <p>{prop.description}</p>
            </div>
        </div>
    );
}

export default Card;